import React from 'react';
import Nav from './Navar';
import './mapstyle.css';

import Button from '@mui/material/Button';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';

import data_4 from '../img/Data_4.jpg';

const Details_4 = () => {

    const main = () => {
        window.location.href = "/"
    }

    const BS4 = () => {
        window.location.href = "/BS4"
    }
    return (
        <div style={{}} >
            <Nav />
            <img src={data_4} className='mintlover' />

            <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'c' }}>

                <ListItem>
                    <ListItemText >
                        <strong>
                        2
                        </strong>

                    </ListItemText>
                </ListItem>
                <ListItem>

                    <ListItemText >
                        <div style={{ fontSize: '14px', color: 'gray' }}>
                           33-20<br />
                            : 9:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~7
                        </div>

                    </ListItemText>

                </ListItem>
                <Divider />
                <ListItem>
                    <ListItemText >
                        <div style={{ fontSize: '11px', color: 'gray' }}>
                        2 1    .<br />
                            2 7  .
                        </div>

                    </ListItemText>
                </ListItem>
                <Divider />
                <ListItem>

                    <ListItemText >

                        <div style={{ fontSize: '11px', color: 'gray' }}>
                            <div style={{ fontSize: '15px' }}> </div>
                            *  2 7  .
                            *,      .
                            *      
                        </div>

                    </ListItemText>
                </ListItem>
            </div>

            <div style={{ marginTop: '180px', marginLeft: '15px' }}>
                <Grid container spacing={2} style={{ marginTop: '10px' }}>
                    <Grid item xs={2}>
                        <Button variant="contained" onClick={main} style={{ backgroundColor: 'gray' }}>
                            <ArrowBackIosIcon />
                        </Button>
                    </Grid>
                    <Grid item xs={10}>
                        <Button onClick={BS4} style={{ backgroundColor: '#CBE23A', color: 'black', width: '90%' }}>
                            
                        </Button>
                    </Grid>
                </Grid>
            </div>

        </div>
    );
};

export default Details_4;