import React from 'react';
import Nav from './Navar';
import './mapstyle.css';
import data_1 from '../img/Data_1.jpg';
import Button from '@mui/material/Button';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import Divider from '@mui/material/Divider';
import Grid from '@mui/material/Grid';


const Details = () => {



    const main = () => {
        window.location.href = "/"
    }

    const bs = () => {
        window.location.href = "/BS"
    }


    return (
        <div style={{}} >
            <Nav />
            <img src={data_1} className='mintlover' />

            <div style={{ display: 'flex', flexDirection: 'column' }}>

                <ListItem>
                    <ListItemText >
                    <strong style={{fontSize:"23px"}}>
                            
                        </strong>

                    </ListItemText>
                </ListItem>
                <ListItem>

                    <ListItemText >
                        <div style={{ fontSize: '14px', color: 'gray' }}>
                              126 22<br />
                            : 9:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~8
                        </div>

                    </ListItemText>

                </ListItem>
                <Divider />
                <ListItem>
                    <ListItemText >
                    <div style={{ fontSize: '12px', color: 'gray',lineHeight:'150%' }}>
                             101 1    .
                            2 6  .
                        </div>

                    </ListItemText>
                </ListItem>
                <Divider />
                <ListItem>

                    <ListItemText >

                        <div style={{ fontSize: '11px', color: 'gray' }}>
                        <div style={{ fontWeight:'bold', fontSize: '15px', marginBottom: '10px'}}> </div>
                            *  2 6  .<br/>
                            *       .<br/>
                            *,      .<br/>
                            *      
                        </div>

                    </ListItemText>
                </ListItem>
            </div>
            <div style={{ marginTop: '160px', marginLeft: '15px' }}>
                <Grid container spacing={2} style={{ marginTop: '10px' }}>
                    <Grid item xs={2}>
                        <Button variant="contained" onClick={main} style={{ backgroundColor: 'gray' }}>
                            <ArrowBackIosIcon />
                        </Button>
                    </Grid>
                    <Grid item xs={10}>
                        <Button onClick={bs} style={{ backgroundColor: '#CBE23A', color: 'black', width: '90%' }}>
                            
                        </Button>
                    </Grid>
                </Grid>

            </div>

           
        </div>
    );
};

export default Details;