import * as React from 'react';
import { Button, Drawer, theme } from 'antd';
import { useState } from 'react';
import Map from './Map';
import Nav from './Navar';
import HorizontalRuleIcon from '@mui/icons-material/HorizontalRule';
import './mapstyle.css';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import Divider from '@mui/material/Divider';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import Typography from '@mui/material/Typography';

function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
}

const rows = [
    createData('e 110 ', ': 1~8'),
    createData('e ', ': 1~6'),
    createData('1  2', ': 4~12'),

];

const App = () => {
    const { token } = theme.useToken();
    const [open, setOpen] = useState(false);
    const showDrawer = () => {
        setOpen(true);
    };
    const onClose = () => {
        setOpen(false);
    };


    return (
        <div >
            <Nav />
            <Map />
            <div>
                <HorizontalRuleIcon type="primary" onClick={showDrawer} style={{ fontSize: '50px', color: '#CBE23A' }} />
            </div>
            <Drawer
                placement="bottom"
                closable={false}
                onClose={onClose}
                open={open}
                getContainer={false}
                style={{ borderTopLeftRadius: '30px', borderTopRightRadius: '30px' }}
            >
                <div>
                    <HorizontalRuleIcon type="primary" onClick={showDrawer} style={{ fontSize: '50px', marginTop: '-20px', color: '#CBE23A' }} />
                </div>
                <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
                    <ListItem>
                        <ListItemText >
                            <div style={{ fontSize: '12px', color: 'gray' }}>
                                 <br/>
                                : 09:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~8
                            </div>
                        </ListItemText>
                    </ListItem>
                    <Divider className='Divider' />
                    <ListItem>
                        <ListItemText >
                            <div style={{ fontSize: '12px', color: 'gray' }}>
                                 <br/>

                                : 09:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~10
                            </div>
                        </ListItemText>
                    </ListItem>
                    <Divider className='Divider' />
                    <ListItem>
                        <ListItemText >
                            <div style={{ fontSize: '12px', color: 'gray' }}>
                                1 <br/>
                                : 09:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~7
                            </div>
                        </ListItemText>
                    </ListItem>
                    <Divider className='Divider' />
                    <ListItem>
                        <ListItemText >
                            <div style={{ fontSize: '12px', color: 'gray' }}>
                                2 <br/>
                                : 09:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~7
                            </div>
                        </ListItemText>
                    </ListItem>

                    <Divider className='Divider' />
                    <ListItem>
                        <ListItemText >
                            <div style={{ fontSize: '12px', color: 'gray' }}>
                                <br/>
                                : 09:00AM~17:00PM &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; :1~5
                            </div>
                        </ListItemText>
                    </ListItem>

                

                </List>

                <Button style={{ backgroundColor: '#CBE23A', color: 'black', width: '90%', justifyContent: 'end' }}>
                    
                </Button>

            </Drawer>
        </div>
    );
};
export default App;